.node {
    white-space: nowrap;
    display: block;
    position: absolute;

    display: inline-block;
    width: -webkit-fill-available;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: fit-content;
}

.nodeValue:hover {
    background-color: azure;
  }

.node-collapsed{
    text-decoration: underline;
    font-weight: bold;
}