#context-menu {
    position: relative;
    z-index: 10000;
    width: fit-content;
    background: black;
    border-radius: 5px;
    display: block;
    font-weight: normal;
}
#context-menu.visible{
    display: block;
}
#context-menu .item {
    padding: 8px 10px;
    font-size: 15px;
    color: white;
    cursor: pointer;
    border-radius: inherit;
    border-bottom: 1px solid gray;
}
#context-menu .item:hover{
    background: gray;
}

.triangle {
    width: 0;
    height: 0;
    border-bottom: 15px solid black;
    border-right: 20px solid transparent;
    margin-bottom: -5px;
    margin-left: 5px;
}
  